/*
 * Global resets
 *
 * Update the foundational and global aspects of the page.
 */

/* Prevent scroll on narrow devices */
html,
body {
  overflow-x: hidden;
  color: #444;
}

html {
  font-family: 'Montserrat', sans-serif;
}

h1 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  color: #313131;
  letter-spacing: -2px;
}

h2, h3, h4, h5, h6 {
  font-family: 'Raleway', sans-serif;
  font-weight: 100;
  color: #313131;
  letter-spacing: -1.5px;
  margin-top: -10px;
  margin-bottom: 32px;
}

a:hover {
  text-decoration: none !important;
  color: #0e6e90 !important;
}

#nav-items a::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background: #0e6e90;
  transition: width .3s;
}

#nav-items a:hover::after {
  width: 100%;
  /* line-height: .85; */
}

